import React, { useState, useEffect } from "react";
import { Routes, Route } from "react-router-dom";
import Main from "./pages/Main";
import WebApp from "@twa-dev/sdk";
import Wallet from "./pages/Wallet";

function App() {
  useEffect(() => {
    WebApp.setHeaderColor("#000");
    WebApp.setBackgroundColor("#000");
  }, []);

  const [balance, setBalance] = useState(100);

  const updateBalance = (newBalance) => {
    setBalance(newBalance);
  };

  const [oldBalance, setOldBalance] = useState(0);

  const updateOldBalance = (newOldBalance) => {
    setOldBalance(newOldBalance);
  };

  const [bet, setBet] = useState(10);

  const updateBet = (newBet) => {
    setBet(parseInt(newBet));
  };

  var bodyElement = document.body;
  let lang;
  try {
    lang = WebApp.initDataUnsafe.user.language_code;
  } catch {
    lang = "en";
  }
  // const lang = WebApp.initDataUnsafe.user.language_code;
  const platform = WebApp.platform;
  if (platform === "ios" || platform === "macos") {
    bodyElement.classList.add(`apple`);
  } else {
    if (platform !== "unknown") {
      bodyElement.classList.add(`material`);
    } else {
      bodyElement.classList.add(`material`);
    }
  }
  return (
    <Routes>
      <Route path="/" element={<Main balance={balance} updateBalance={updateBalance} bet={bet} updateBet={updateBet} />} />
      <Route path="/wallet" element={<Wallet />} />
    </Routes>
  );
}

export default App;
