import { useState, useEffect } from "react";
import { useNavigate } from "react-router-dom";
import { BackButton } from "@twa-dev/sdk/react";
import appState from "../../store/store";
import { action } from "mobx";

const Wallet = action(() => {
  const navigate = useNavigate();

  const [asset, setAsset] = useState("deposit");

  const updateAsset = (newAsset) => {
    setAsset(newAsset)
  }

  return (
    <>
      <BackButton onClick={() => navigate("/")} />
      <div className="page scrolling">
        <div className="container relative">
          <div className="page__content">
            <div className="HFG3 LK2z LLxw">
              <div onClick={() => updateAsset("deposit")} className={`KJ3x LI2z ${asset === "withdraw" && `opacity-[0.3]`}`}>
                <div className="BJh2 JHGX">
                  <img src={require("../../assets/images/deposit_card.png")} alt=""/>
                </div>
                <div className="GF2p">Deposit</div>
              </div>
              <div onClick={() => updateAsset("withdraw")} className={`KJ3x LI2z ${asset === "deposit" && `opacity-[0.3]`}`}>
                <div className="BJh2 JHGX">
                <img src={require("../../assets/images/withdraw_card.png")} alt=""/>
                </div>
                <div className="GF2p">Withdraw</div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
});

export default Wallet;
